  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}
.Btn {
  cursor: pointer;
  position: relative;
  background-color: transparent;
  border: none;
  height : 35px;
  width: 100px;
  font-weight: 600;
  transition: all 0.2s linear 0.2s;
  border-radius: 0px;
  overflow: hidden;
  align-self: center;
  justify-self: center;
}
.main {
    scroll-behavior: smooth;
    /* min-height: 100vh; */
   
}

.lined {
    color: rgb(255, 255, 255);
  opacity: 1;
  list-style: none;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  cursor: pointer;
  transition: all 0.3s linear;
}
.lined:hover{ 
   opacity: 1;
}
.lined::after {
  position: relative;
  content: "";
  display: block;
  right: 10px;
  bottom: 20px;
  width: 3px;
  height: 0px;
  background: #fff;
  transition: height 0.3s linear;
}
.lined:hover::after {
  height: 20px;
  /* width: 100%; */
  transition: height 0.3s linear;
}

