#header {
    position: relative;
    width: 100vw;
    height: 80vh;
    margin-top: 50px;
  }
  
  #header h1 {
    margin: 0 0 10px 0;
    font-size: 48px;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.815);
  }
  
  #header .brand-name {
    color: #3498db;
  }
  
  #header h2 {
    color: #484848;
    font-size: 24px;
  }
  
  #header .btn-get-started {
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: 0.5s;color: #3498db;
    border: 2px solid #3498db;
    text-decoration: none;
  }
  
  #header .btn-get-started:hover {
    color: #fff;
    background: #3498db;
  }
  
  .header-img {
    text-align: right;
  }
  
  #header .animated {
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
  }
  
  @keyframes up-down {
    0% {
      -webkit-transform: translateY(10px);
              transform: translateY(10px);
    }
  
    100% {
      -webkit-transform: translateY(-10px);
              transform: translateY(-10px);
    }
  }
  
  @-webkit-keyframes up-down {
    0% {
      -webkit-transform: translateY(10px);
              transform: translateY(10px);
    }
  
    100% {
      -webkit-transform: translateY(-10px);
              transform: translateY(-10px);
    }
  }
  
  .cart-span {
    border-radius: 10px;
  }
